// Êñ∞ÂàõÂª∫‰∏Ä‰∏™axiosÂÆû‰æãÔºåÂπ∂ËøõË°åÂü∫Á°ÄÈÖçÁΩÆ
const instance = axios.create({
  baseURL: getBaseUrl(),
  timeout: 10000,
  headers: {
    'Content-Type': 'application/json',
    // "Content-Type":"application/x-www-form-urlencoded;charset=utf-8"
  },
});

// Ê∑ªÂä†ËØ∑Ê±ÇÊã¶Êà™Âô®
instance.interceptors.request.use(
  config => {
    // ÂÜçÊ¨°ËÆæÁΩÆtokenÊàñËÄÖÊ∑ªÂä†loadingÁ≠âËØ∑Ê±ÇÂâçÁöÑÊìç‰Ωú
    // Ê∑ªÂä†‰∏Ä‰∏™loading
    // store.dispatch(actions.showLoading(true));
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

// Ê∑ªÂä†xiÂìçÂ∫îÊã¶Êà™Âô®
instance.interceptors.response.use(
  response => {
    // ÂìçÂ∫îÊï∞ÊçÆÂêéÂÅöÁÇπ‰ªÄ‰πà
    // Ê∑ªÂä†‰∏Ä‰∏™loading
    // store.dispatch(actions.showLoading(false));
    // Â≠òÂú®‰∏öÂä°ÂºÇÂ∏∏ ÊäõÂá∫Áªô‰∏öÂä°‰ª£Á†ÅÂéªÊçïËé∑
    const { data } = response;
    if (data.code != 200) {
      const msg = data.msg || data.message || '';
      message.warning(msg);
      return Promise.reject(data);
    }
    // ‰∏çÂ≠òÂú®‰∏öÂä°ÂºÇÂ∏∏ËøîÂõûÊï∞ÊçÆ
    return data;
  },
  error => {
    if (error && error.response) {
      const { status } = error.response;
      console.warn(
        `http error: status-${status} message-${codeMessage[status]}`
      );

      if (codeMessage[status] !== null || undefined) {
        notification.warning({
          placement: 'topRight',
          duration: 2,
          message: 'ÊèêÁ§∫',
          description: `Êé•Âè£Ôºö${error.response.data.path}Ôºå${error.response.data.message}`,
        });
      } else {
        notification.warning({
          placement: 'topRight',
          duration: 2,
          message: 'ÊèêÁ§∫',
          description: error,
        });
      }
    }
    return Promise.reject(error); // Èùû‰∏öÂä°ÂºÇÂ∏∏Êó†ÈúÄÊäõÂá∫ÈîôËØØ ÂÜÖÈÉ®ÂêûÊéâ
  }
);

/**
 * getËØ∑Ê±Ç
 * @method get
 * @param {url, params, loading} ËØ∑Ê±ÇÂú∞ÂùÄÔºåËØ∑Ê±ÇÂèÇÊï∞ÔºåÊòØÂê¶ÈúÄË¶ÅÂä†ËΩΩÂ±Ç
 */
const get = (url: string, params: any) => {
  return new Promise((resolve, reject) => {
    // {
    //   params: params
    // }
    instance
      .get(url, params)
      .then(res => {
        resolve(res);
      })
      ['catch'](err => {
        reject(err);
      });
  });
};
/**
 * postËØ∑Ê±Ç
 * @method post
 * @param {url, params} ËØ∑Ê±ÇÂú∞ÂùÄÔºåËØ∑Ê±ÇÂèÇÊï∞ÔºåÊòØÂê¶ÈúÄË¶ÅÂä†ËΩΩÂ±Ç
 */
const post = (url: string, data: any) => {
  return new Promise((resolve, reject) => {
    // qs.stringify(data)
    instance
      .post(url, data)
      .then(res => {
        console.log(res);
        resolve(res);
      })
      ['catch'](err => {
        reject(err);
      });
  });
};

function request<T>(config: AxiosRequestConfig): Promise<Response<T>> | null {
  let newUrl = config.url;
  if (
    (config.method === 'GET' ||
      config.method === 'DELETE' ||
      config.method === 'PUT') &&
    config.data
  ) {
    const param: string[] = [];
    Object.keys(config.data).map((v: string, k: number) => {
      param.push(`${v}=${encodeURIComponent(config.data[v])}`);
    });
    const paramStr = param.join('&');
    newUrl = `${newUrl}?${paramStr}`;
  }
  const curConfig = { ...config, url: newUrl };
  return (instance.request<Response<T>>(curConfig) as any) as Promise<
    Response<T>
  >;
}
export { get, post, request, Response };
